export default (state = {}, action) => {
    switch(action.type){
        case 'SET_CONTENT':
            return state = {
                all: action.data,
                ...state
            }
        case 'SET_CATEGORY':
            return state = {
                ...state,
                category: action.data                
            }
        
        case 'SET_CATEGORY_CONTENT':
            return state = {
                all: state.all,
                category: state.category,
                cat_content: state.cat_content,
                single: state.single,
                mycontent: action.data                
            }
        // case 'SET_PAGE_CONTENT':
        //     let page_content = [];
        //     for (let i=action.data-1;i<=action.data+11;i++) {
        //         page_content.push(state.all[i])
        //     }
        //     return state.page_content;
    
        case 'MY_CONTENT':
            // state.mycontent = action.data;
            return state = {
                all: state.all,
                category: state.category,
                cat_content: state.cat_content,
                single: state.single,
                mycontent: action.data                
            }
        case 'SINGLE_CONTENT':
            return state = {
                ...state.all,
                ...state.category,
                ...state.cat_content,
                ...state.mycontent,
                single: action.data                
            }
        case 'GET_SINGLE': 
            return state.single = Object.values(state.all).map((value) => value._id === action.id);
        default:
            return state;
    }
}